
import Exploration from './Exploration';
//import TestExploration from './TestExploration';
import AdderExploration from './AdderExploration';
import MultiplierExploration from './MultiplierExploration';

function createCanvas(width: number, height: number): HTMLCanvasElement {
    const canvas: HTMLCanvasElement = document.createElement("canvas");
    canvas.width = width;
    canvas.height = height;
    return canvas;
}


function createExploration(id: string, width: number, height: number, type: typeof Exploration): Exploration {
    const element = document.getElementById(id);
    if (!element) {
        throw new Error("Document element " + id + " not found.");
    }

    const canvas = createCanvas(width, height);
    element.appendChild(canvas);

    const exploration = new type(canvas);
    exploration.update();
    canvas.addEventListener("click", function(event) {
        exploration.onClick(event.offsetX, event.offsetY);
    });

    return exploration;
}


const canvas1: HTMLCanvasElement = createCanvas(600, 400);
document.getElementById('1')!.appendChild(canvas1);


// Explorations
const adderExploration = createExploration('1', 640, 480, AdderExploration);
const multiplierExploration = createExploration('2', 640, 480, MultiplierExploration);

function renderLoop() {
    adderExploration.render();
    multiplierExploration.render();
    requestAnimationFrame(renderLoop);
}
renderLoop();
